<template>

  <div>
    <nav-header @change="cityChange"></nav-header>
    <div class="accessory-result-page accessory-page">
      <div class="container">
        <div class="filter-nav">
          <span class="sortby">Sort by:</span>
          <a href="javascript:void(0)" v-bind:class="{default:true,cur:!sortFlag}" @click="sortRooms(false)">Default</a>
          <a href="javascript:void(0)" v-bind:class="{price:true,cur:sortFlag}" @click="sortRooms(true)">Price
            <svg class="icon icon-arrow-short">
              <use xlink:href="#icon-arrow-short"></use>
            </svg>
          </a>
          <a href="javascript:void(0)" class="filterby stopPop" @click.stop="showFilterPop">Filter by</a>
        </div>
        <div class="accessory-result">
          <!-- filter -->
          <div class="filter stopPop" id="filter" v-bind:class="{'filterby-show':filterBy}">
            <dl class="filter-price">
              <dt>Source:</dt>
              <dd><a href="javascript:void(0)" @click="setSourceFilter('all')"
                     v-bind:class="{'cur':sitesChecked=='all'}">All</a>
              </dd>
              <dd v-for="(value,key) in sites">
                <a href="javascript:void(0)" @click="setSourceFilter(key)" v-bind:class="{'cur':sitesChecked==key}">{{value}}</a>
              </dd>
            </dl>
          </div>
          <!-- search result accessories list -->
          <div class="accessory-list-wrap">
            <div class="accessory-list col-4">
              <ul>
                <li v-for="item in pageList">
                  <a href="javascript:void(0);" @click="showItem(item)">
                    <div class="pic">
                      <img v-lazy="item.img" alt="">
                    </div>
                    <div class="main">
                      <div class="title"><h2 class="short-text">{{item.title}}</h2></div>
                      <div class="detail short-text">{{item.detail}}</div>
                      <div class="other-detail">
                        <div class="area">{{item.area}}</div>
                        <div class="size">{{item.size}}</div>
                        <div class="type">{{item.type}}</div>
                      </div>
                    </div>
                    <div class="price" v-show="item.price"><span>{{item.price}}</span>元</div>
                    <div class="source">{{item.source}}</div>
                  </a>
                </li>
              </ul>
            </div>
            <pagination :pageInfo="pageInfo" @change="pageChange"></pagination>
          </div>
        </div>
      </div>
    </div>
    <modal v-bind:mdShow="mdShowTips" @close="closeModal">
      <p slot="message">
        {{mdShowMessage}}
      </p>
    </modal>
    <modal v-bind:mdShow="mdShowInfos" @close="closeModal">
      <div slot="message" class="info-modal">
        <div class="info-title"><h3>{{checkItem.title}}</h3></div>
        <div class="info-detail">{{checkItem.detail}}</div>
        <div class="info-img"><img v-lazy="checkItem.img" :alt="checkItem.title"></div>
        <ul class="info-right">
          <li class="info-area" v-show="checkItem.area"><span>区域：</span>{{checkItem.area}}</li>
          <li class="info-size" v-show="checkItem.size"><span>大小：</span>{{checkItem.size}}</li>
          <li class="info-type" v-show="checkItem.type"><span>类型：</span>{{checkItem.type}}</li>
          <li class="info-pubtime" v-show="checkItem.pub_time"><span>发布时间：</span>{{checkItem.pub_time}}</li>
        </ul>
      </div>
      <div slot="btnGroup">
        <a class="btn btn--m" v-bind:href="checkItem.url" target="_blank">查看详情</a>
      </div>
    </modal>
    <div class="md-overlay" v-show="overLayFlag" @click.stop="closePop"></div>
    <nav-footer><span class="last-time">{{ last_time }}</span></nav-footer>
  </div>

</template>

<script>
  import './../assets/css/base.css'
  import './../assets/css/product.css'
  import NavHeader from '@/components/NavHeader'
  import NavFooter from '@/components/NavFooter'
  import Modal from '@/components/Modal'
  import pagination from '@/components/vue-pagination'
  import axios from 'axios'

  export default {
    name: "rooms-list",
    data() {
      return {
        allrooms: [],   // 记录所有房源的列表
        selectSite: [],  // 点击某一房源来源后的列表
        nowSite: [],  // 当前展示房源列表
        pageList: [],   // 分页展示列表
        // baseUrl: 'http://118.24.147.149/api/v1/',
        baseUrl:this.GLOBAL.host+'/api/v1/',
        sites: {},
        sitesChecked: 'all',
        sortFlag: false,
        mdShowTips: false,   // 显示切换城市等待页面
        mdShowInfos: false,
        mdShowMessage: '',
        checkItem: '',
        website: {'douban': '豆瓣小组', 'wuba': '58同城', 'ziru': '自如', 'hizhu': '嗨住'},
        pageInfo: {
          total: 0,  // 记录总条数   默认空，如果小于pageNum则组件不显示   类型Number
          current: 1,  // 当前页数，   默认为1                             类型Number
          pagenum: 10, // 每页显示条数,默认10                              类型Number
          pagegroup: 5,    // 分页条数     默认为5，需传入奇数                 类型Number
          page: 1,   // 尾页页码
          skin: '#16a086'  // 选中页码的颜色主题 默认为'#16a086'               类型String
        },
        last_time: '',
        filterBy: false,
        overLayFlag: false,
      }
    },
    components: {
      NavHeader,
      NavFooter,
      pagination,
      Modal,
    },
    mounted: function () {
      this.getRoomsList('sz');
    },
    watch: {
      nowSite: function () {
        this.pageList = this.nowSite.slice(0, this.pageInfo.pagenum);
        this.pageInfo.total = this.nowSite.length;
        this.pageInfo.page = Math.ceil(this.nowSite.length / this.pageInfo.pagenum);
        this.pageInfo.current = 1;
      }
    },
    methods: {
      getRoomsList(city) {
        let url = this.baseUrl + city + '/rooms';  // 拼接url
        this.sites = {};    // 清空元素
        this.allrooms = [];
        this.sortFlag = false;
        this.sitesChecked = 'all';
        this.mdShowTips = true;
        this.mdShowMessage = '正在获取数据中,请稍候... ...';
        axios.get(url).then((result) => {
          var res = result.data;
          if (res.status == 0) {
            const room_datas = res.result;
            for (var key in room_datas) {
              this.sites[key] = this.website[key];   // 取出属性
              let temp_datas = room_datas[key];
              for (var i = 0; i < temp_datas.length; i++) {
                temp_datas[i]['source'] = this.website[key];
              }
              this.allrooms.push(...temp_datas);
            }
            this.nowSite = this.allrooms.slice();
            this.nowSite.sort(() => Math.random() > .5);
            this.selectSite = room_datas;
            this.last_time = res.last_time.slice(0, 19);
            this.mdShowTips = false;
          } else {
            this.mdShowMessage = res.msg;
          }
        }).catch((error) => {
          this.mdShowMessage = '服务器访问错误,请稍后再重试... ...';
        });
      },
      setSourceFilter(key) {
        window.scrollTo({"behavior": "smooth", "top": 10});
        if (key == 'all') {
          this.nowSite = this.allrooms.slice();
          this.nowSite.sort(() => Math.random() > .5);
        } else {
          this.nowSite = this.selectSite[key];
        }
        this.sitesChecked = key;
        this.sortFlag = false;
      }
      ,
      sortRooms(flag) {
        if (flag != this.sortFlag) {
          if (flag) {
            this.nowSite.sort((a, b) => {
              if (a.price == 'undefined') {
                return -1;
              }
              if (b.price == 'undefined') {
                return 1;
              }
              return a.price - b.price;
            });
          } else {
            this.nowSite.sort(() => Math.random() > .5);
          }
          this.sortFlag = flag;
        }
      }
      ,
      pageChange(current) {     // 页码改变传入新的页码，此处做回调
        this.pageList = this.nowSite.slice((current - 1) * this.pageInfo.pagenum, current * this.pageInfo.pagenum);   // 改变页面显示列表
        window.scrollTo({"behavior": "smooth", "top": 10});   // 跳回顶部
      }
      ,
      cityChange(city) {
        this.getRoomsList(city);
      }
      ,
      showItem(checkItem) {
        this.mdShowInfos = true;
        this.checkItem = checkItem;
      }
      ,
      gotoSite(url) {
        this.mdShowInfos = false;
        console.log(url);
      }
      ,
      closeModal() {
        this.mdShowTips = false;
        this.mdShowInfos = false;
      }
      ,
      showFilterPop() {
        this.filterBy = true;
        this.overLayFlag = true;
      }
      ,
      closePop() {
        this.filterBy = false;
        this.overLayFlag = false;
      }
      ,
    }
  }
</script>
<style scoped>
  .accessory-list.col-4 .source {
    position: absolute;
    top: -10px;
    right: -5px;
    color: #adacac;
    -webkit-transform: rotate(9deg);
    -moz-transform: rotate(9deg);
    -ms-transform: rotate(9deg);
    -o-transform: rotate(9deg);
    transform: rotate(9deg);
  }

  .info-modal {
    position: relative;
    text-align: center;
    width:inherit;
    height:100%;
  }

  .info-img img {
    max-height:200px;
    max-width: 60%;
  }

  .info-detail {
    line-height: 16px;
    font-size: 15px;
    text-align: left;
    text-indent: 2em;
    max-height:192px;
    overflow: hidden;
    text-overflow: ellipsis;
    padding:5px 0;
  }
  .info-right{
    min-width:60%;
    position: absolute;
    right:-60%;
    top:50%;
  }
  .info-right span{
    float:left;
  }
  .info-right li{
    display:block;
    background-color: #fff;
    padding:10px;
    margin:10px;
  }
  @media only screen and (max-width: 767px){
    .info-right{
      display:none;
    }
  }

</style>




